<template>
	<div class="partdetails">
		<div class="partdetails-header clearfix">
			<div @click="goback" class="partdetails-header-return"><img src="../../static/return.png">返回</div>
			<div class="partdetails-header-center">兼职详情</div>
			
		</div>
		<div class="partdetails-content">
			<div class="partdetails-content-header clearfix">
				<div class="partdetails-content-header-title">知名公司招聘市场推销员</div>
				<div class="partdetails-content-header-zhaomu">招募中</div>
				<div class="partdetails-content-header-baoming">已报名:0人</div>
			</div>
			<div class="partdetails-content-bottom">
				<div class="partdetails-content-bottom-text">薪资：100元/一天</div>
				<div class="partdetails-content-bottom-text">类型：简单易做</div>
				<div class="partdetails-content-bottom-text">结算：日结</div>
			</div>
		</div>
		<div class="partdetails-bottom">
			<div class="partdetails-bottom-header clearfix">
				<div class="partdetails-bottom-header-frame">
					<img src="../../static/钱.png">钱太少<span>0</span>
				</div>
				<div class="partdetails-bottom-header-frame">
					<img src="../../static/时间.png">时间紧<span>0</span>
				</div>
				<div class="partdetails-bottom-header-frame">
					<img src="../../static/贬.png">不靠谱<span>0</span>
				</div>
				<div class="partdetails-bottom-header-frame">
					<img src="../../static/远.png">离我远<span>0</span>
				</div>
			</div>
			<div class="partdetails-detailed">
				<div class="partdetails-detailed-title">
					职业描述
				</div>
				<div class="partdetails-detailed-fuli">
					<div class="partdetails-detailed-fuli-frame">
						包工作餐
					</div>
					<div class="partdetails-detailed-fuli-frame">
						包住宿
					</div>
					<div class="partdetails-detailed-fuli-frame">
						交通补贴
					</div>
					<div class="partdetails-detailed-fuli-frame">
						有提成
					</div>
				</div>
			</div>
		</div>
	</div>
	
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import globals from '@/components/Global'
import BScroll from 'better-scroll'
Vue.prototype.$http = axios
	export default {
		data() {　　　　　　
			return {　　
				
		        jobids:[]
			}　　　　
		},
		methods:{
			goback(){
				this.$router.go(-1);
			},
			
		},
		created() {
			this.jobid = this.$route.query.jobid
			console.log(this.jobid)
			let dataurl1=globals.dataUrl+'job-info'
			let _this = this
		    axios.post(dataurl1,{key:this.jobid})
					.then(function(response) {
						_this.Lists = response.data;
						
					})
					.catch(function(error) {
						console.log(error);
					});
			
		}
	}
	
</script>

<style>
	.partdetails{background: #f7f7f7;position: fixed;top: 0;bottom: 0;right: 0;left: 0;overflow-y: scroll;z-index: 10;}
	.partdetails-header{height: 0.8rem;width: 100%;background:#5487d6;color: white;}
	.partdetails-header-return{width:1.3rem ;height: 0.8rem;line-height: 0.8rem;font-size: 0.3rem;float: left;}
	.partdetails-header-return img{width:0.2rem ;float: left;margin:0.21rem 0.1rem;}
	.partdetails-header-center{width:5rem;text-align: center;line-height: 0.8rem;float: left;font-size: 0.36rem;}
	.partdetails-content{width: 100%;background: white;font-family: "黑体";border-bottom: 1px solid #dbdcde;}
	.partdetails-content-header{padding: 0.4rem 0 0.3rem 0;box-sizing: border-box;border-bottom:1px dotted #CCCCCC;margin-left: 0.3rem;}
	.partdetails-content-header-title{height: 0.34rem;float: left;width: 5.8rem;font-size: 0.32rem;font-weight: bold;margin-bottom: 0.25rem;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;}
	.partdetails-content-header-zhaomu{margin-bottom: 0.25rem;float: right;margin-right: 0.2rem;font-size: 0.28rem;height: 0.34rem;border: 1px solid #5487d6;line-height: 0.34rem;color: #5487d6;text-align: center;padding: 0 0.1rem;}
	.partdetails-content-header-baoming{float: left;color: #a8a8a8;font-size: 0.3rem;}
	.partdetails-content-bottom{padding: 0.35rem 0 0.3rem 0.3rem;}
	.partdetails-content-bottom-text{width: 100%;font-size: 0.28rem;margin-bottom: 0.1rem;color: #5487d6;}
	.partdetails-bottom{border:1px solid #dbdcde ;margin-top: 0.2rem;background: white;}
	.partdetails-bottom-header{background: #f9fafc;padding: 0.25rem 0.8rem 0 0.8rem;box-sizing: border-box;color: #8d8d97;}
	.partdetails-bottom-header-frame{height: 0.36rem;line-height: 0.36rem;width: 50%;text-align: center;float: left;font-size: 0.3rem;margin-bottom: 0.2rem;cursor: pointer;}
	.partdetails-bottom-header-frame img{width: 0.36rem;float: left;margin-left: 0.5rem;}
	.partdetails-bottom-header-frame span{margin: 0 0.2rem;}
	.partdetails-detailed{background: white;padding: 0.55rem 0.3rem;}
	.partdetails-detailed-title{font-size: 0.36rem;font-weight: bold;height: 1rem;}
</style>


