<template>
	<div class="index-list-frame" ref="foodsWrapper">
		
		<div class="index-list-frame-list" v-for="(list,index) in Lists">
			<router-link :to="{path:'/PartDetails',query:{jobid:list.jobid}}" style="display: block;" >
				<img class="frame-list-img" :src="list.listsrc">
				<div class="frame-list-1 clearfix">
					<div class="frame-list-title">{{list.title}}</div>
					
				</div>
				<div class="frame-list-2 clearfix">
					
					<div class="frame-list-time">已报名{{list.participate}}</div>
					<div class="frame-list-money">{{list.money}}</div>
				</div>
				<div class="frame-list-2 clearfix">
					
					<div class="frame-list-baoming" v-show='list.geren != ""'>{{list.recruittype}}</div>
					<div class="frame-list-day">{{list.days}}</div>
				</div>
			</router-link>
		</div>
		
	</div>
</template>
<script>
	import axios from 'axios'
import Vue from 'vue'
import globals from '@/components/Global'
import BScroll from 'better-scroll'
Vue.prototype.$http = axios
// let serverUrl = '/api/' //本地调试时 
//let serverUrl = 'http://jz.bellairehc.com/' //打包部署上线时 


export default {
	data() {　　　　　　
		return {　　
			
	        listHeight: [],
	        foodsScrollY: 0,
			selectedFood:"",
			Lists:[
				{
					jobid:"",
					listsrc:'https://wo732511064.github.io/Part-time-job/static/zhaopin.png',
					days:"123",
					money:'100元/天',
					participate:'0',
					recruittype:'个人',
					title:'发传单活动'
				}
			]
		}　　　　
	},
	created() {
	    let dataurl=globals.dataUrl+'item-list'
		let _this = this
		this.list = this.$route.query.Lists
		let testStr = encodeURIComponent(JSON.stringify(this.list));
	    axios.post(dataurl)
				.then(function(response) {
					_this.Lists = response.data;
				})
				.catch(function(error) {
					console.log(error);
				});
		this.$nextTick(() => {
	        this._initScroll(); // 初始化scroll
	        this._calculateHeight(); // 初始化列表高度列表
	      })
		
	  },
	
	methods: {
		_initScroll() {
	      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
	        click: true,
	        probeType: 3
	      });
	      // 监控滚动事件
	      this.foodsScroll.on('scroll', (pos) => {
	        this.foodsScrollY = Math.abs(Math.round(pos.y))
	      })
	    },
	    _calculateHeight() {
	      let foodList = this.$refs.foodsWrapper.querySelectorAll('.index-list-frame-list')
	      let height = 0
	      this.listHeight.push(height)
	      for (let i = 0, l = foodList.length; i < l; i++) {
	        let item = foodList[i]
	        height += item.clientHeight
	        this.listHeight.push(height)
	      }
	    },
	  },
	
	
}

</script>


