<template>
	<div class="index">
		<div class="header">
			<div class="header-select">
				<router-link to='' class="header-address">广州▼</router-link>
				<span></span>
				<input type="search" placeholder="查找">
				<img src="../../static/search.png">
			</div>
		</div>
		
		<div class="index-top clearfix">
			
			<div class="index-top-chunk"><div class="index-top-ico ye"><img src="../../static/金钱.png"></div>高薪全职</div>
			<div class="index-top-chunk"><div class="index-top-ico or"><img src="../../static/附近.png"></div>离你最近</div>
			<div class="index-top-chunk"><div class="index-top-ico bl"><img src="../../static/游戏.png"></div>边玩边赚</div>
			<div class="index-top-chunk"><div class="index-top-ico pu"><img src="../../static/上班.png"></div>极速上岗</div>
			<div class="index-top-chunk"><div class="index-top-ico re"><img src="../../static/招聘.png"></div>周末急招</div>
		</div>
		<div class="index-list">
			<div class="index-list-header">
				
                	<div class="index-list-header-frame" @click.prevent.stop="listShow()" 
	                        @keydown.prevent.40="arrowDown1" @keydown.prevent.38="arrowUp1">
	                    {{selectItem}}
	                    <span  :class="['triangle',showList==false?'triangleShow':'triangleHidden']"></span>
	                </div>
	                <div class="index-popur" v-if="showList" @click="showList=false">
		                <div class="index-list-block"  @click.prevent.stop>
		                    
		                    <div v-for="item in newObj" class="index-list-block-line" @click="selectOption(item)" >  {{item.type}} </div>
		                </div>
	            	</div>
                   
                   
	           
	                <div class="index-list-header-frame" @click.prevent.stop="listShow2()" 
	                        @keydown.prevent.40="arrowDown2" @keydown.prevent.38="arrowUp2">
	                    {{selectItem2}}
	                    <span  :class="['triangle',showList2==false?'triangleShow':'triangleHidden']"></span>
	                </div>
	                <div class="index-popur" v-if="showList2" @click="showList2=false">      	
		                <div class="index-list-block" @click.prevent.stop>
		                    <div v-for="item in newObj2" class="index-list-block-line" @click="selectOption2(item)">  {{item.type}} </div>
		                </div>
	            	</div>
	            
	            
	                <div class="index-list-header-frame" @click.prevent.stop="listShow3()" 
	                        @keydown.prevent.40="arrowDown3" @keydown.prevent.38="arrowUp3">
	                    {{selectItem3}}
	                    <span  :class="['triangle',showList3==false?'triangleShow':'triangleHidden']"></span>
	                </div>
	                <div class="index-popur" v-if="showList3" @click="showList3=false">
		                <div class="index-list-block" @click.prevent.stop>
		                    <div v-for="item in newObj3" class="index-list-block-line" @click="selectOption3(item)">  {{item.type}} </div>
		                </div>
	            	</div>
			</div>
			
            <router-view></router-view>
        </div>    
		<!--<div class="index-list">
			<div class="index-list-header" id="index-list-header">
				<div v-for="(item,index) in tabs" class="index-list-header-frame" :class="{active:index == num}"  @click="tab(index)" >{{item.select}}<span>▲</span> </div>
				
				
			</div>
			<div v-for="(item,index) in tabs" class="index-popur" :class="{active:index == num}"   v-html="item.content" > </div>
			
		</div>-->
		
		
	</div>
</template>
<style>
.index-popur{z-index:1;position:absolute;left:0;width:100%;top:1.2rem;height:100%;background:rgba(0,0,0,.5);overflow:hidden;transition:all .3s}
.index-list-block{background:#fff;padding:0 .2rem;box-sizing:border-box;width:100%;color:#666;background:#fff}
.index-list-block-line{height:.8rem;width:100%;font-size:.3rem;border-bottom:1px solid #ccc;line-height:.8rem}
.index-list-block-line:last-child{border:0}
.triangle{display:inline-block;position:absolute;top:.4rem;right:.05rem;cursor:pointer}
.triangleHidden{border-left:.1rem solid transparent;border-right:.1rem solid transparent;border-bottom:.16rem solid #676F7F}
.triangleShow{border-left:.1rem solid transparent;border-right:.1rem solid transparent;border-top:.16rem solid #676F7F}
</style>
<script>
	import Vue from 'vue'
	export default{
		data(){
            return {
                showList:false,
                obj:[
                    {type:"类型"},
                    {type:"招募状态"},
                ],
                filterText:"",
                selectItem:"简单易做",

                showList2:false,
                obj2:[
                    {type:"类型1"},
                    {type:"招募状态1"},
                ],
                filterText2:"",
                selectItem2:"跑腿办事",
                
                showList3:false,
                obj3:[
                    {type:"类型2"},
                    {type:"招募状态2"},
                ],
                filterText3:"",
                selectItem3:"技能职业"
            };

　　　　},
　　methods: {
//      tab(index) {
//          this.num = index;
//         
//      }
			listShow(){
                this.showList=!this.showList;
                if (this.showList2) {
                    this.showList2 = false;
                }
                if (this.showList3) {
                    this.showList3 = false;
                }
            },
            selectOption(item){
                this.selectItem=item.type;
                this.showList=false;
               
            },
            // 第一个下拉列表 按键：向下的箭头
            arrowDown1(e){
                if (!this.showList) {
                    this.showList = true;
                }
                if (this.showList2) {
                    this.showList2 = false;
                }
                if (this.showList3) {
                    this.showList3 = false;
                }
            },
            // 第一个下拉列表 按键：向上的箭头
            arrowUp1(e){
                if (this.showList) {
                    this.showList = false;
                }
                if (this.showList2) {
                    this.showList2 = false;
                }
                if (this.showList3) {
                    this.showList3 = false;
                }
            },
            listShow2(){
                this.showList2=!this.showList2;
                if (this.showList) {
                    this.showList = false;
                }
                if (this.showList3) {
                    this.showList3 = false;
                }
            },
            selectOption2(item){
                this.selectItem2=item.type;
                this.showList2=false;
            },
            // 第二个下拉列表 按键：向下的箭头
            arrowDown2(e){
                if (!this.showList2) {
                    this.showList2 = true;
                }
                if (this.showList) {
                    this.showList = false;
                }
                if (this.showList3) {
                    this.showList3 = false;
                }
            },
            // 第一个下拉列表 按键：向上的箭头
            arrowUp2(e){
                if (this.showList2) {
                    this.showList2 = false;
                }
                if (this.showList) {
                    this.showList = false;
                } 
                if (this.showList3) {
                    this.showList3 = false;
                }
            },
            listShow3(){
                this.showList3=!this.showList3;
                if (this.showList) {
                    this.showList = false;
                }
                if (this.showList2) {
                    this.showList2 = false;
                }
            },
            selectOption3(item){
                this.selectItem3=item.type;
                this.showList3=false;
            },
            // 第二个下拉列表 按键：向下的箭头
            arrowDown3(e){
                if (!this.showList3) {
                    this.showList3 = true;
                }
                if (this.showList) {
                    this.showList = false;
                }
                if (this.showList2) {
                    this.showList2 = false;
                }
            },
            
            arrowUp3(e){
                if (this.showList3) {
                    this.showList3 = false;
                }
                if (this.showList) {
                    this.showList = false;
                } 
                if (this.showList2) {
                    this.showList2 = false;
                }
            },
            
   },
    computed:{
            newObj:function(){
                let self = this;
                return self.obj.filter(function (item) {
                    return item.type.toLowerCase().indexOf(self.filterText.toLowerCase()) !== -1;
                })
            },
            newObj2:function(){
                let self = this;
                return self.obj2.filter(function (item) {
                    return item.type.toLowerCase().indexOf(self.filterText2.toLowerCase()) !== -1;
                })
            },
            newObj3:function(){
                let self = this;
                return self.obj3.filter(function (item) {
                    return item.type.toLowerCase().indexOf(self.filterText3.toLowerCase()) !== -1;
                })
            }
        },
        watch:{
            '$store.getters.getClickRootNum': function () {
                if (this.showList){
                    this.showList = false;
                }
                if (this.showList2) {
                    this.showList2 = false;
                }
                if (this.showList3) {
                    this.showList3 = false;
                }
            }
        }
 }

	
</script>


